

import * as React from "react";
import { z } from "zod";
import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

/* -------- Schema -------- */
export const loginSchema = z.object({
  username: z
    .string()
    .min(1, "Username or email is required.")
    .max(254, "Too long."),
  password: z
    .string()
    .min(6, "Password must be at least 6 characters.")
    .max(128, "Too long."),
});

export type LoginPayload = z.infer<typeof loginSchema>;

/* -------- Props -------- */
interface LoginFormProps extends Omit<React.ComponentProps<"div">, "onSubmit"> {
  onSubmit?: (data: LoginPayload) => void;
  loading?: boolean;
  errorMessage?: string; // server/api error
}

/* -------- Component -------- */
export function LoginForm({
  className,
  onSubmit,
  loading = false,
  errorMessage,
  ...props
}: LoginFormProps) {
  const [values, setValues] = React.useState<LoginPayload>({
    username: "",
    password: "",
  });

  const [fieldErrors, setFieldErrors] = React.useState<
    Partial<Record<keyof LoginPayload, string>>
  >({});

  const handleChange = React.useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const { name, value } = e.target;
      setValues((prev) => ({ ...prev, [name]: value }));
      setFieldErrors((prev) => ({ ...prev, [name]: undefined }));
    },
    []
  );

  const handleSubmit = React.useCallback(
    (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      const parsed = loginSchema.safeParse(values);
      if (!parsed.success) {
        const errs: Partial<Record<keyof LoginPayload, string>> = {};
        for (const issue of parsed.error.issues) {
          const path = issue.path[0] as keyof LoginPayload | undefined;
          if (path) errs[path] = issue.message;
        }
        setFieldErrors(errs);
        return;
      }
      onSubmit?.(parsed.data);
    },
    [onSubmit, values]
  );

  return (
    <div className={cn("flex flex-col gap-6", className)} {...props}>
      <Card className="overflow-hidden p-0">
        <CardContent className="grid p-0 md:grid-cols-2">
          <form className="p-6 md:p-8" onSubmit={handleSubmit} noValidate>
            <div className="flex flex-col gap-6">
              {/* Header */}
              <div className="flex flex-col items-center text-center">
                <h1 className="text-2xl font-bold">Welcome back</h1>
                <p className="text-muted-foreground text-balance">Log in to your account</p>
              </div>

              {/* Username */}
              <div className="grid gap-1.5">
                <Label htmlFor="username">Username or Email</Label>
                <Input
                  id="username"
                  name="username"
                  type="text"
                  placeholder="yourname or you@example.com"
                  autoComplete="username"
                  required
                  aria-invalid={!!fieldErrors.username}
                  aria-describedby={fieldErrors.username ? "username-error" : undefined}
                  value={values.username}
                  onChange={handleChange}
                />
                {fieldErrors.username && (
                  <p id="username-error" className="text-destructive text-xs mt-1">
                    {fieldErrors.username}
                  </p>
                )}
              </div>

              {/* Password */}
              <div className="grid gap-1.5">
                <div className="flex items-center">
                  <Label htmlFor="password">Password</Label>
                  <a
                    href="#"
                    className="ml-auto text-sm underline-offset-2 hover:underline"
                  >
                    Forgot your password?
                  </a>
                </div>
                <Input
                  id="password"
                  name="password"
                  type="password"
                  placeholder="Password"
                  autoComplete="current-password"
                  required
                  aria-invalid={!!fieldErrors.password}
                  aria-describedby={fieldErrors.password ? "password-error" : undefined}
                  value={values.password}
                  onChange={handleChange}
                />
                {fieldErrors.password && (
                  <p id="password-error" className="text-destructive text-xs mt-1">
                    {fieldErrors.password}
                  </p>
                )}
              </div>

              {/* API Error */}
              {errorMessage && (
                <p role="alert" className="text-destructive text-sm mt-2">
                  {errorMessage}
                </p>
              )}

              {/* Submit */}
              <Button type="submit" className="w-full" disabled={loading} aria-busy={loading}>
                {loading ? "Logging in..." : "Login"}
              </Button>

              {/* Divider */}
              <div className="after:border-border relative text-center text-sm after:absolute after:inset-0 after:top-1/2 after:z-0 after:flex after:items-center after:border-t">
                <span className="bg-card text-muted-foreground relative z-10 px-2">Or continue with</span>
              </div>

              {/* Social Auth */}
              <SocialButtons />
            </div>
          </form>

          <SideImage />
        </CardContent>
      </Card>

      <TermsFooter />
    </div>
  );
}

/* Subcomponents reused */
function SocialButtons() {
  return (
    <div className="grid grid-cols-3 gap-4">
      {["Apple", "Google", "Meta"].map((provider) => (
        <Button key={provider} variant="outline" type="button" className="w-full">
          <span className="sr-only">Login with {provider}</span>
          <svg aria-hidden="true" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" />
        </Button>
      ))}
    </div>
  );
}

function SideImage() {
  return (
    <div className="bg-muted relative hidden md:block">
      <img
        src="/login.svg"
        alt="Login illustration"
        className="absolute inset-0 h-full w-full object-fit dark:brightness-[0.2] dark:grayscale p-5"
      />
    </div>
  );
}

function TermsFooter() {
  return (
    <div className="text-muted-foreground text-center text-xs text-balance *:[a]:underline *:[a]:underline-offset-4 *:[a]:hover:text-primary">
      By clicking continue, you agree to our <a href="#">Terms of Service</a> and <a href="#">Privacy Policy</a>.
    </div>
  );
}
